import React from 'react'
import Menu from "components/Navbars/Menu.js";
import Footer from "components/Footers/Footer.js";
import SimpleImageSlider from "react-simple-image-slider";
import { db } from "./firebase-config";
import { collection, getDocs} from "firebase/firestore";
import { useState, useEffect } from "react";

const Projekti = () => {
  const [users, setUsers] = useState([]);
  const usersCollectionRef = collection(db, "projekti");

  useEffect(() => {
    const getUsers = async () => {
      const data = await getDocs(usersCollectionRef);
      setUsers(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    };

    getUsers();
  }, []);
  return (
    <>
    <Menu fixed/>
    {users.map(obj  => 
  <div className="container px-4 mx-auto" style={{marginBottom: "10%"}}>
  <div className="flex flex-wrap">
    <div className="w-full px-4 flex-1">
      <span className="text-sm block my-4 p-3 text-blueGray-700 rounded border border-solid border-blueGray-100">
      <h6 className="text-xl mb-1 font-semibold text-center uppercase">
      {obj.naslov}
                      </h6>
                     </span>
    </div>
    <div className="w-full"></div>
    <div className="w-full px-4 flex-1">
      <span className="text-sm block my-4 p-3 text-blueGray-700 rounded border border-solid border-blueGray-100 md:w-6/12"><SimpleImageSlider
        width={430}
        height={400}
        images={obj.slike}
        showBullets={true}
        showNavs={true}
      /></span>
    </div>
    <div className="w-full px-4 flex-1">
      <span className="text-sm block my-4 p-3 text-blueGray-700 rounded border border-solid border-blueGray-100">
      {obj.paragrafi.map(paragraf  => 
                      <p className="mb-4 text-blueGray-500">
                      {paragraf}
                      </p> )}
 

</span>
    </div>
  </div>
</div>  )}
    <Footer />
    </>
  )
}

export default Projekti